




















import {Component, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import PanelContent from "@/components/BuilderUI/panel/rightpanel/RightPanelContent.vue";

@Component({
  name: "ShortcutMenu",
  components: {
    PanelContent,
    Draggable
  }
})
export default class ShortcutMenu extends Vue {
  tabs: Array<any> = [];

  created() { this.tabs = this.$children; }

  clickedTab(clickedTab: any) {
    this.tabs.forEach(tab => tab.isActive = tab.name === clickedTab.name )
  }
}
