





























































































































































import {Component, Vue, Watch} from 'vue-property-decorator';

import BuilderNavbar from "@/components/BuilderUI/navbar/BuilderNavbar.vue";
import ShortcutMenu from "@/components/BuilderUI/shortcutmenu/ShortcutMenu.vue";
import ShortcutContent from "@/components/BuilderUI/shortcutmenu/ShortcutContent.vue";
import {OMS} from "@/OMS";
import {Color, Position} from "@/BuefyUtils";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";
import {Media, MediaType} from "@/template/Media";

@Component({
  name: "MediaManager",
  components: {ShortcutContent, ShortcutMenu, Navbar: BuilderNavbar},
})

export default class MediaManager extends Vue {

  isCalling = false;
  isModalActive = false;
  isUploading = false;

  isAddActive = false;
  isEditActive = false;

  newData: Media = new Media(0, "", "", MediaType.IMAGE, "", "");
  selected = {};

  // @ts-ignore
  file: File = {};

  mediaList: Array<Media> = [];

  beforeMount() {}

  mounted() {
    this.getAll();
  }

  async getAll() {
    this.isCalling = true;
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.mediaList = crud.getResult();
    this.isCalling = crud.status;
  }

  async onCreate() {
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(this.newData);
    await crud.shoot({});

    this.isAddActive = false;
    await this.getAll();
  }

  async onEdit(media: Media) {
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.UPDATE);
    crud.setAttachData(media);
    await crud.shoot({});

    this.isEditActive = false;
    await this.getAll();
  }

  async onDelete(media: Media) {
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.DELETE);
    await crud.shoot({id: media.id});

    await this.getAll();
  }

  // @ts-ignore
  getFileExtension(fileName: string = this.file.name | 'Error Name') {
    return fileName.substr(fileName.lastIndexOf('.') + 1).toLowerCase();
  }

  async onSubmitUpload() {

  }

  @Watch('file')
  async onUpload() {
    console.log("SELECTED FILE");
    try {
      switch (this.getFileExtension(this.file.name).toUpperCase()) {
        case "JPEG":
        case "PNG":
        case "GIF":
        case "MP4":
        case "AVI":
          console.log("THIS FILE CAN UPLOAD");

          const formData = new FormData();
          formData.append('file', this.file);

          const crud = new CRUD(Category.MEDIA);
          crud.setAttachData(formData);
          crud.setRequest(Request.CREATE);
          await crud.shoot({})

          this.isModalActive = false;
          break;
        default:
          console.log("CANT UPLOAD THIS FILE");
          OMS.showToast("ระบบไม่รองรับชนิดไฟล์ที่เลือก", Color.DANGER, Position.TOP, 4000);
          // @ts-ignore
          this.file = {};
          break;
      }
    } catch (error) {
      //console.log(error);
    }
  }
}

